import Slider from "react-slick"

import "./cardSlider-slick.scss"; 
import "./cardSlider-slick-theme.scss";

import Img1 from "../../../assets/product/Rectangle 309.png"
import Img2 from "../../../assets/product/Rectangle 310.png"
import Img3 from "../../../assets/product/Rectangle 311.png"
import Img4 from "../../../assets/product/Rectangle 312.png"

const Images = [Img1,Img2,Img3]

const CardSlider = () => {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
      };

    return (
        <Slider {...settings}>
            {Images.map(img => (
            <div key={img} className="slider-item-img">
                <img src={img} alt=""/>
            </div>
            ))}
        </Slider>
    )
}

export default CardSlider;