import React, { useEffect, useState } from "react";
import "./CryptoStatistics.scss"

const Cryptocurrencies = [
    {
        name:"Internet Computer",
        price:"3.53",
        discount:"1.85"
    },
    {
        name:"Cosmos Hub",
        price:"7.20",
        discount:"3.64"
    },
    {
        name:"Filecoin",
        price:"3.33",
        discount:"3.55"
    },
    {
        name:"Lido DAO",
        price:"1.64",
        discount:"3.37"
    },
    {
        name:"Cornos",
        price:"0.051407",
        discount:"0.56"
    },
    {
        name:"Aptos",
        price:"5.81",
        discount:"4.44"
    },
    {
        name:"VeChain",
        price:"0.015",
        discount:"3.60"
    },
    {
        name:"Lido DAO",
        price:"1.64",
        discount:"3.37"
    },
]



function CryptoStatistics() {

    const [isWindowWidth, setIsWindowWidth] = useState(window.screen.availWidth)

    const setWrapperWidth = () => {
        const block = document.getElementById("wrapper-blok")
        const width = ((isWindowWidth / Cryptocurrencies.length)+ 5) * Cryptocurrencies.length
        block.style.width = `${width}px`
        console.log(block.style.width)
        if(width > 2000){
            document.getElementById("wrapper-span")
            document.querySelector(".crypto-statistics-wrapper__line").appendChild(document.getElementById("wrapper-span").cloneNode(true))
        }
    }

    useEffect(() => (
        setWrapperWidth()
    ),[isWindowWidth])



    return (
        <div className="crypto-statistics-wrapper">
            <div className="crypto-statistics-wrapper__container">
                <div id="wrapper-blok" className="crypto-statistics-wrapper__block">
                    <div className="crypto-statistics-wrapper__line to-left">
                        <span id="wrapper-span" className="wrapper__line-span">
                            {Cryptocurrencies.map(Cryptocurrency =>(  
                                <article className="running-line__element" key={Cryptocurrencies.indexOf(Cryptocurrency)}>
                                    <h3 className="running-line__name">{Cryptocurrency.name}</h3>
                                    <p className="running-line__price">${Cryptocurrency.price}</p>
                                    <span className="running-line__discount">-{Cryptocurrency.discount}%</span>
                                </article>   
                            ))} 
                        </span>
                        <span className="wrapper__line-span">
                            {Cryptocurrencies.map(Cryptocurrency =>(  
                                <article className="running-line__element" key={Cryptocurrencies.indexOf(Cryptocurrency)}>
                                    <h3 className="running-line__name">{Cryptocurrency.name}</h3>
                                    <p className="running-line__price">${Cryptocurrency.price}</p>
                                    <span className="running-line__discount">-{Cryptocurrency.discount}%</span>
                                </article>   
                            ))} 
                        </span>
                    </div>
                </div> 
            </div>
        </div>
    )
}

export default CryptoStatistics